"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import {
  useQuery,
  useMutation,
  useQueryClient,
  QueryClient,
  QueryClientProvider,
} from "@tanstack/react-query";
import axios from "axios";
import {
  Plus,
  Download,
  Upload,
  RefreshCw,
  MessageSquare,
  CheckCircle,
} from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { useToast } from "@/components/ui/use-toast";
import { format } from "date-fns";
import React from "react";
import CircularProgress from "@mui/material/CircularProgress";

/* ---------- axios ---------- */
axios.defaults.baseURL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000";
axios.interceptors.request.use((cfg) => {
  const t = localStorage.getItem("access_token");
  if (t) cfg.headers.Authorization = `Bearer ${t}`;
  return cfg;
});

/* ---------- query client ---------- */
const queryClient = new QueryClient();

/* ---------- types ---------- */
type Task = {
  id: number;
  no: number | null;
  dept: string | null;
  bank: string | null;
  issue: string | null;
  responsible: string | null;
  status: string;
  remark: string | null;
  created_at: string;
  updated_at: string;
};

type Update = {
  id: number;
  status: string | null;
  update_text: string | null;
  created_at: string;
  updated_by_name: string;
};

/* ---------- role hook ---------- */
function useRole() {
  const [role, setRole] = useState<string>("");
  React.useEffect(() => {
    const token = localStorage.getItem("access_token");
    if (!token) return;
    try {
      const payload = JSON.parse(atob(token.split(".")[1]));
      setRole(payload.role ?? "");
    } catch {
      setRole("");
    }
  }, []);
  return role;
}

/* ---------- main component ---------- */
function UitTaskBoard() {
  const router = useRouter();
  const { toast } = useToast();
  const role = useRole();
  const [selected, setSelected] = useState<Task | null>(null);
  const [updateText, setUpdateText] = useState("");
  const [updateStatus, setUpdateStatus] = useState("");

  /* ---------- queries ---------- */
  const { data = [], isLoading, refetch } = useQuery<Task[]>({
    queryKey: ["uit-tasks"],
    queryFn: () => axios.get("/uit-tasks").then((r) => r.data),
  });

  const { data: updates = [] } = useQuery<Update[]>({
    queryKey: ["uit-updates", selected?.id],
    queryFn: () => axios.get(`/uit-tasks/${selected?.id}/updates`).then((r) => r.data),
    enabled: !!selected,
  });

  /* ---------- mutations ---------- */
  const createMut = useMutation({
    mutationFn: (data: Partial<Task>) => axios.post("/uit-tasks", data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["uit-tasks"] });
      toast({ title: "Task created" });
    },
  });

  const updateMut = useMutation({
    mutationFn: (data: { status?: string; update_text?: string }) =>
      axios.post(`/uit-tasks/${selected!.id}/updates`, data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["uit-updates", selected?.id] });
      setUpdateText("");
      setUpdateStatus("");
      toast({ title: "Update added" });
    },
  });

  /* ---------- helpers ---------- */
  const badgeColor = (s: string) =>
    s === "Done"
      ? "bg-green-100 text-green-800"
      : s === "Pending"
      ? "bg-yellow-100 text-yellow-800"
      : "bg-gray-100 text-gray-800";

  /* ---------- render ---------- */
  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto">
        {/* header */}
        <div className="flex items-center justify-between mb-6">
          <h1 className="text-3xl font-bold text-gray-800">UIT Task Board</h1>
          <div className="flex gap-2">
            {role === "superadmin" && (
              <>
                <Dialog>
                  <DialogTrigger asChild>
                    <Button>
                      <Plus className="mr-2 h-4 w-4" /> Add Task
                    </Button>
                  </DialogTrigger>
                  <DialogContent>
                    <DialogHeader>
                      <DialogTitle>New UIT Task</DialogTitle>
                    </DialogHeader>
                    <form
                      onSubmit={(e) => {
                        e.preventDefault();
                        const fd = new FormData(e.currentTarget);
                        createMut.mutate({
                          no: fd.get("no") ? Number(fd.get("no")) : null,
                          dept: fd.get("dept"),
                          bank: fd.get("bank"),
                          issue: fd.get("issue"),
                          responsible: fd.get("responsible"),
                          status: fd.get("status") || "Open",
                          remark: fd.get("remark"),
                        });
                      }}
                    >
                      <div className="grid gap-4 py-4">
                        <Input name="no" placeholder="No (optional)" type="number" />
                        <Input name="dept" placeholder="Dept" />
                        <Input name="bank" placeholder="Bank" />
                        <Textarea name="issue" placeholder="Issue" />
                        <Input name="responsible" placeholder="Responsible" />
                        <select name="status" className="border rounded px-3 py-2">
                          <option value="Open">Open</option>
                          <option value="Pending">Pending</option>
                          <option value="Done">Done</option>
                        </select>
                        <Textarea name="remark" placeholder="Remark" />
                      </div>
                      <Button type="submit">Create</Button>
                    </form>
                  </DialogContent>
                </Dialog>

                <Button variant="outline" onClick={() => axios.get("/uit-tasks/download", { responseType: "blob" }).then((res) => {
                  const url = window.URL.createObjectURL(new Blob([res.data]));
                  const link = document.createElement("a");
                  link.href = url;
                  link.setAttribute("download", "uit_tasks.xlsx");
                  document.body.appendChild(link);
                  link.click();
                  link.remove();
                })}>
                  <Download className="mr-2 h-4 w-4" /> Export
                </Button>
              </>
            )}

            <Button variant="ghost" onClick={() => refetch()}>
              <RefreshCw className="mr-2 h-4 w-4" />
              Refresh
            </Button>
          </div>
        </div>

        {/* table */}
        <div className="bg-white rounded-xl shadow p-4">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>No</TableHead>
                <TableHead>Dept</TableHead>
                <TableHead>Bank</TableHead>
                <TableHead>Issue</TableHead>
                <TableHead>Responsible</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Updated</TableHead>
                <TableHead className="w-[100px]">Action</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {isLoading ? (
                <TableRow>
                  <TableCell colSpan={8} className="text-center">
                    <CircularProgress />
                  </TableCell>
                </TableRow>
              ) : data.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={8} className="text-center">No tasks</TableCell>
                </TableRow>
              ) : (
                data.map((task) => (
                  <TableRow key={task.id} className="hover:bg-gray-50">
                    <TableCell>{task.no ?? "-"}</TableCell>
                    <TableCell>{task.dept ?? "-"}</TableCell>
                    <TableCell>{task.bank ?? "-"}</TableCell>
                    <TableCell className="max-w-xs truncate" title={task.issue ?? ""}>
                      {task.issue ?? "-"}
                    </TableCell>
                    <TableCell>{task.responsible ?? "-"}</TableCell>
                    <TableCell>
                      <Badge className={badgeColor(task.status)}>{task.status}</Badge>
                    </TableCell>
                    <TableCell>{format(new Date(task.updated_at), "dd/MM/yyyy HH:mm")}</TableCell>
                    <TableCell>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => setSelected(task)}
                      >
                        <MessageSquare className="h-4 w-4" />
                      </Button>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </div>

        {/* detail / updates drawer */}
        {selected && (
          <div className="mt-6 bg-white rounded-xl shadow p-4">
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-xl font-semibold">Task #{selected.id} Updates</h2>
              <Button size="sm" onClick={() => setSelected(null)}>Close</Button>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
              <div>
                <p><strong>Dept:</strong> {selected.dept ?? "-"}</p>
                <p><strong>Bank:</strong> {selected.bank ?? "-"}</p>
                <p><strong>Responsible:</strong> {selected.responsible ?? "-"}</p>
              </div>
              <div>
                <p><strong>Issue:</strong> {selected.issue ?? "-"}</p>
                <p><strong>Status:</strong> <Badge className={badgeColor(selected.status)}>{selected.status}</Badge></p>
                <p><strong>Remark:</strong> {selected.remark ?? "-"}</p>
              </div>
            </div>

            {/* add update */}
            <div className="flex gap-2 mb-4">
              <select
                className="border rounded px-3 py-2"
                value={updateStatus}
                onChange={(e) => setUpdateStatus(e.target.value)}
              >
                <option value="">Choose status</option>
                <option value="Open">Open</option>
                <option value="Pending">Pending</option>
                <option value="Done">Done</option>
              </select>
              <Textarea
                className="flex-1"
                placeholder="Update text"
                value={updateText}
                onChange={(e: { target: { value: React.SetStateAction<string>; }; }) => setUpdateText(e.target.value)}
                rows={2}
              />
              <Button
                onClick={() =>
                  updateMut.mutate({ status: updateStatus, update_text: updateText })
                }
                disabled={!updateText.trim()}
              >
                <CheckCircle className="mr-2 h-4 w-4" /> Add
              </Button>
            </div>

            {/* timeline */}
            <div className="max-h-96 overflow-y-auto">
              {updates.length === 0 ? (
                <p className="text-gray-500 text-sm">No updates yet</p>
              ) : (
                <div className="space-y-3">
                  {updates.map((u) => (
                    <div key={u.id} className="border-l-2 pl-3">
                      <div className="flex items-center gap-2">
                        <span className="font-medium">{u.updated_by_name}</span>
                        <Badge className={badgeColor(u.status ?? "")}>{u.status ?? "—"}</Badge>
                        <span className="text-xs text-gray-500">
                          {format(new Date(u.created_at), "dd/MM/yyyy HH:mm")}
                        </span>
                      </div>
                      <p className="text-sm text-gray-700">{u.update_text ?? "—"}</p>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default function UitTaskPage() {
  return (
    <QueryClientProvider client={queryClient}>
      <UitTaskBoard />
    </QueryClientProvider>
  );
}